.filters{
    padding: 16px 0px;
    display: flex;
    flex-wrap: wrap;

}
.filters-item{
    padding: 8px;
    display: flex;
    cursor: pointer;
    background-color: var(--primary-bg);
    color: var(--filter-text);
    border: 1px solid var(--gray);
    border-radius: 8px;
    margin: 10px 10px 10px 0px;
    font-size: 14px;
}
.filters-name{
    margin-left: 5px;
}